"use client";

import Input from "@/components/ui/Input/Input";
import { Button, Heading } from "@radix-ui/themes";
import { useState } from "react";

export default function Home() {
  const [login, setLogin] = useState<boolean>(false);
  const [formData, setFormData] = useState<{ user: string; password: string }>({
    user: "",
    password: "",
  });
  const handleSetLogin = () => {
    setLogin(!login);
  };
  return (
    <div className="flex items-center justify-center h-screen px-[16px]">
      <div className="flex flex-col gap-2 w-full items-center">
        <Heading>{login ? "Вход" : "Регистрация"}</Heading>
        <br />
        <Input
          name={"formData"}
          type={"text"}
          placeholder={"Логин"}
          value={formData.user}
          onChange={(e) => setFormData({ ...formData, user: e.target.value })}
          label={"Логин"}
        />
        <Input
          name={"formData.password"}
          type={"text"}
          placeholder={"Пароль"}
          value={formData.password}
          onChange={(e) =>
            setFormData({ ...formData, password: e.target.value })
          }
          label={"Пароль"}
          maxLength={8}
        />
        <button onClick={handleSetLogin}>
          {login ? (
            <p className="text-gray-400 text-sm w-full">
              Если вы не зарегистрированы -{" "}
              <p className="text-black">Регистрация</p>
            </p>
          ) : (
            <p className="text-gray-400 text-sm w-full">
              Если у вас есть аккаунт - <p className="text-black">Войти</p>
            </p>
          )}
        </button>
        <Button radius="full" color="red">
          {login ? "Войти" : "Создать"}
        </Button>
      </div>
    </div>
  );
}
